import { Base16 } from './utils/Base16';
import { Base64 as Base64Converter } from './utils/Base64';
import { GBK } from './utils/GBK';
import { useState } from 'react';
import { DownOutlined } from '@ant-design/icons';
import { Flex, Input, Dropdown, Button, message, Breadcrumb } from 'antd';
import { useMediaQuery, useWindowSize } from "@uidotdev/usehooks";
import { menuItems, menuItems2 } from "./MenuItems";
import type { MenuProps } from 'antd';
const { TextArea } = Input;

function Base64() {

    const dropdownItems = [
        {
            key: '1',
            label: 'UTF-8',
        },
        {
            key: '2',
            label: 'GBK',
        },
        {
            key: '3',
            label: 'Hex',
        }
    ]
    const [method, setMethod] = useState('UTF-8');
    const handleMenuClick: MenuProps['onClick'] = (e) => {
        let item = dropdownItems.find(i => i?.key === e.key);
        if (item) {
            setMethod(item.label);
        }
    };
    const menuProps = {
        items: dropdownItems,
        onClick: handleMenuClick,
    };

    function encode() {


        let text: string = (document.getElementById('text') as any).value;

        let bytes = new Uint8Array();
        if (method === 'UTF-8') {
            bytes = new TextEncoder().encode(text);
        }
        else if (method === 'GBK') {
            bytes = GBK.gbkEncode(text);
        }
        else if (method === 'Hex') {
            bytes = Base16.fromBase16(text);
        }
        text = Base64Converter.toBase64(bytes);

        message.info('');
        (document.getElementById('text2') as any).value = text;
    };

    function decode() {
        let text: string = (document.getElementById('text') as any).value;

        let bytes: Uint8Array;
        try {
            bytes = Base64Converter.fromBase64(text);
        } catch (error) {
            message.error('');
            return;
        }
        if (method === 'UTF-8') {
            text = new TextDecoder().decode(bytes);
        }
        else if (method === 'GBK') {
            text = GBK.gbkDecode(bytes);
        }
        else if (method === 'Hex') {
            text = Base16.toBase16(bytes);
        }

        message.info('');
        (document.getElementById('text2') as any).value = text;
    };

    const windowSize = useWindowSize();
    const style: any = { height: 120, resize: 'none', minWidth: 400 };
    if (windowSize.width) {
        if (windowSize.width <= 768)
            style.width = windowSize.width - 80;
        else
            style.width = windowSize.width - 280;
    }
    const isSmallDevice = useMediaQuery("only screen and (max-width : 768px)");

    return (
        <Flex gap="middle" vertical align="center" style={{ margin: isSmallDevice ? '40px 40px' : '40px 0' }} >
            <Breadcrumb
                items={[
                    {
                        title: '',
                    },
                    {
                        title: 'Base',
                        menu: { items: menuItems },
                    },
                    {
                        title: 'Base64',
                        menu: { items: menuItems2 },
                    }
                ]}
            />
            <h2>Base64</h2>
            <TextArea style={style} id='text' />
            <Flex gap="middle" vertical={false} align='center'>
                <label></label>
                <Dropdown menu={menuProps}>
                    <Button style={{ width: 120 }} >
                        <label>{method}</label>
                        <DownOutlined />
                    </Button>
                </Dropdown>
                <Button type="primary" onClick={encode}></Button>
                <Button type="primary" onClick={decode}></Button>
            </Flex >
            <TextArea disabled style={style} id='text2' />
        </Flex >
    )
}

export default Base64;