/**
 * CommercialCode
 */
export class CommercialCode {

    static DATA = ''

    /**
     * (4)
     */
    public static fromCodeString(str: string): string {
        if (str.length % 4 !== 0) {
            throw new Error('');
        }

        let result = '';
        for (let i = 0; i < str.length; i += 4) {
            const codeString = str.substring(i, i + 4);
            let code = parseInt(codeString);
            if (!Number.isNaN(code)) {
                result += CommercialCode.DATA[code];
            }
        }
        return result;
    }

    /**
     * 
     */
    public static toCodesString(str: string): string {
        let result = '';

        for (const character of str) {

            var code = CommercialCode.DATA.indexOf(character);
            if (code == -1) {
                code = 0;
            }

            result += code.toString().padStart(4, '0');
        }

        return result;
    }
}
