/**
 * Base64
 */
export class Base64 {

    /**
     * Uint8ArrayBase64
     */
    public static toBase64(bytes: Uint8Array): string {
        return btoa(String.fromCharCode(...bytes));

    }

    /**
     * Base64Uint8Array
     */
    public static fromBase64(str: string): Uint8Array {
        return Uint8Array.from(atob(str), (c) => c.charCodeAt(0));
    }
}
